@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
.header {
  text-align: center;
  border: 1px solid #f3f3f3;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding: 0;
  display: inline-block;
}

.header__nav {
  display: inline-block;
}

.header__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.header__list-item:not(:first-child) {
  margin-left: 20px;
}

.header__link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.header__link:hover, .header__link .is-active, .header .is-active {
  color: #00acdc;
}

.header .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}

* {
  box-sizing: border-box;
  transition: all .3s;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
}

.main {
  grid-template-rows: repeat(2, 408px);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

